import React, { ReactElement, useCallback } from 'react';
import { TournamentSettings, TournamentStatus } from '../interfaces';
import CSV from 'papaparse';

import styles from '../css/crosstable.module.css';
import { MdExitToApp } from 'react-icons/md';
import { POST } from '../utils/requests';

interface PasteInputProps {
    closePasteInput: () => void
    status: TournamentStatus
    settings: TournamentSettings
    id: string
    updatePlayerDict: () => void
}

export default function PasteInput(props: PasteInputProps): ReactElement {

    const requireLastName = false;

	const handleSubmit = useCallback((e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        let form = e.target as HTMLFormElement;
        let textArea = form.textArea as HTMLTextAreaElement;
        let text = textArea.value;
		if (!text.trim()) return;
		if (!text.split('\n').length && text.split(',').length && text.split('\t').length) return;
		let json = CSV.parse(text, {
			delimitersToGuess: [',', '\t'],
			dynamicTyping: true,
			fastMode: true
		} as CSV.ParseConfig) as CSV.ParseResult;
		if (json.data.length > 150) return console.error('Can\'t paste more than 150 players at once!');
		let data = json.data.map((row) => {
			let [firstName, rating]: [string, number] = row;
			try {
				if (typeof firstName !== 'string') throw [firstName, 'string'].join(',');
				if (typeof rating !== 'number') throw [rating, 'number'].join(',');
				return {firstName, rating};
			} catch (error) {
				if (!text.split('\n').length) return null;
				let e = error.split(',');
				console.error(new TypeError('Bad input on row ' + row.join(',') + '\n' + e[0] + ' is not type ' + e[1]));
				return null;
			}
		})
            .filter(r => r);
		POST({
			url: 'tournament/' + props.id + '/appendPlayers',
			data
		}); /*
		POST({
			url: 'tournament/' + props.id + '/appendPlayersHack',
			data: json.data
		})
			.then(() => Promise.all([props.updatePlayerDict()]));*/
	}, [props.id, requireLastName, props.updatePlayerDict]);
		
    let title = `CSV player data entry for tournament '${props.status.name || props.status.id}'`;
    return (
        <>				
            <div className={styles.titles}>
                <div>{title}</div>
                <div className={['button', styles.closeButton].join(' ')} onClick={props.closePasteInput}>
                    <MdExitToApp />
                </div>
            </div>
            <div className={styles.subheader}>
                Please paste your input in csv (comma-separated values) format
            </div>
            <div className={styles.container}>
                <form
                    className={styles.form}
                    target='_self'
                    onSubmit={handleSubmit}
                    method='POST'
                >
                    <textarea className={styles.textarea} name='textArea'/>                
                    <button
                        type='submit'
                        className={styles.submit}
                    >
                        SUBMIT
                    </button>
                </form>
            </div>
        </>
    );
}