import React, { ReactElement, useState, useEffect, useCallback, useMemo, useReducer } from 'react';
import styles from '../css/pairings.module.css';
import { TournamentStatus, ID, Player, PairingObject } from '../interfaces';
import { SinglePairing } from '../components/';
import { GET, POST } from '../utils/requests';
import { AiOutlineLoading3Quarters } from 'react-icons/ai';
import { TiThMenu } from 'react-icons/ti';
import { PairingDetails } from '../components/SinglePairing';
import { TournamentSettings } from '../utils/settings';

interface PairingsProps {
	userRound: number
	status: TournamentStatus
	playerDict: Map<ID, Player>
	updatePlayerDict: () => void
    playerDictKey: string
	settings: TournamentSettings

	id: string
	canTriggerNextRound: boolean
	setDisplayPlayer: (id: string) => void
	toggleSidebar: () => void
	toggleRightTab: () => void

	updateRound: () => void
	setLastRound: (override: boolean) => void
	toggleCrossTable: () => void
	lastRoundOverride: boolean
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
}

export default function Pairings(props: PairingsProps): ReactElement {

	const round = useMemo(() => props.userRound || props.status.round, [props.userRound, props.status.round]);
    
	const [resultsState, updateResults] = useReducer(() => Math.random().toString(16).slice(2, 9), '');
	const reduceResults = useCallback((state: Map<string, [number | null, number | null]>, {id, W, B}: {
		id: string
		W: Player,
		B: Player
	}): Map<string, [number | null, number | null]> => {
		if (W && B) if (!W.histories[round] || !B.histories[round]) console.log(round, W.histories);
		state.set(id, [
			W ? W.histories ? W.histories[round][props.settings.displayPoints] : null : 0,
			B ? B.histories ? B.histories[round][props.settings.displayPoints] : null : 0
		]);
		updateResults({});
		return state;
	}, [round, props.settings.displayPoints]);

	const [pairingData, setPairings] = useState([] as PairingObject[]);
	const [results, setResults] = useReducer(reduceResults, new Map() as Map<string, [number | null, number | null]>);
	const [loading, setLoading] = useState(false);


	const getPairings = useCallback((): Promise<void> => {
		if (!round) return;
		if (!props.playerDict.size) return;
		return GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round }
		})
			.then((pairings: PairingObject[]) => {
				for (let [w, b] of pairings) {
					setResults({
						id: w + '.' + b, 
						W: props.playerDict.get(w),
						B: props.playerDict.get(b)
					});
				}
				setPairings(pairings);
			})
			.catch(console.error);
	}, [props.id, setPairings, setResults, round, props.playerDict, props.playerDictKey]);

	function getDisplayName(id: ID, player: Player): string {
		if (id === 'bye') return 'BYE';
		if (!player) return id;
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [player.firstName, player.lastName].join(' ');
		else displayName = player.firstName;
		return `${displayName} [${player.score}]`;
	}
	
	let pending = Array.from(results.values())
		.filter(arr => arr.every(v => v === null));
			
	const pairRemaining = useCallback((): Promise<void> => {
		return POST({
			url: '/tournament/' + props.id + '/getRestPairings'
		})
			.then(props.updatePlayerDict)
			.catch(console.error);
	}, [getPairings, props.id]);
			
	const triggerNextRound = useCallback((): Promise<void> => {
		setLoading(true);
		return POST({
			url: '/tournament/' + props.id + '/getNextPairings'
		})
			.then((pairings: PairingObject[]) => setPairings(pairings))
			.then(() => {
				getPairings();
				props.updatePlayerDict();
			})
			.then(props.updateRound)
			.catch(console.error)
			.finally(() => setLoading(false));
	}, [setLoading, setPairings, props.id, getPairings, props.updateRound, setLoading]);

	const canPairMore = useMemo((): boolean => {
		return Array.from(props.playerDict.values()).filter(p => p.histories && !p.histories[round]).length >= 2;
	}, [props.playerDict, props.playerDictKey]);

	function generatePairButton(): ReactElement {
		return (
			<div
				className={[styles.nextButton, 'button'].join(' ')}
				onClick={pairRemaining}
			>
				Pair Remaining
			</div>
		);
	}

	function generateNextButton(): ReactElement {
		let lastRound: boolean;
		if (round >= props.settings.totalRounds) lastRound = true;
		else if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
		else lastRound = false;

		return loading ? (
			<div
				className={[styles.nextButton, 'button'].join(' ')}
			>
				<AiOutlineLoading3Quarters />
			</div>
		) : round < props.status.round ? (
			<div
				className={[styles.waitButton].join(' ')}
			/>
		) : !props.canTriggerNextRound ? (
			<div className={[styles.waitButton].join(' ')}>
				Must have at least 2 active players
			</div>
		) : pending.length > 0 ? (
			<div className={[styles.waitButton].join(' ')} >
				{`Awaiting ${pending.length} result${pending.length > 1 ? 's' : ''}`}
			</div>
		) : lastRound ? (
			<div
				className={[styles.finishButton, 'button'].join(' ')}
				onClick={props.toggleCrossTable}
			>
				Final Standings
			</div>
		) : (
			<div
				className={[styles.nextButton, 'button'].join(' ')}
				onClick={triggerNextRound}
			>
				Next Round
			</div>
		);
	}
	
	function validateForm(form: HTMLFormElement): boolean | string | void {
		if (form.resultWhite.value === '' && form.resultBlack.value === '') return undefined;
		if (!Array.from(form).every((input) => (input as HTMLInputElement).validity.valid)) return false;
		if (!props.settings.gamePointTotal) return true;
		let nW = Number(form.resultWhite.value);
		let nB = Number(form.resultBlack.value);
		let total = props.settings.displayPoints === 'game' ? props.settings.gamePointTotal : 1;
		if (nW + nB === total) return true;
		return 'Setting: sum results to ' + props.settings.gamePointTotal;
	}
    
	const [expanded, setExpanded] = useState('');

	function Listing(): ReactElement[] {
		let pairings = [] as ReactElement[];
		let dict = props.playerDict;
		let i = 1;
		for (let [w, b, gameLinks] of pairingData) {
	
			let [white, black] = [dict.get(w), dict.get(b)];
			let [wDisplay, bDisplay] = [getDisplayName(w, white), getDisplayName(b, black)];			
			let id = w + '.' + b;
			let result = results.get(id);
			if (!result) continue;
			pairings.push(
				<div id={id} className={styles.pairing} key={['pairings', w, b].join('.')} >
					<div
						className={['playerInfo', styles.playerInfo, w === 'bye' ? styles.bye : ''].join(' ')}
						id={w}
						onClick={(e: React.MouseEvent<HTMLDivElement>) => w === 'bye' ? null : props.setDisplayPlayer((e.target as HTMLDivElement).id)}
					>{wDisplay}</div>
					<SinglePairing
						expand={(set: boolean) => setExpanded(set ? id : '')}
						expanded={expanded === id}
						result={result}
						gameLinks={gameLinks}
						tournamentID={props.id}
						inputClassName={styles.inputResult}
						hidden={{
							W: w,
							B: b,
							round
						}}
						validate={validateForm}
						handleSubmit={async (e) => {
							let form = e.target as HTMLFormElement & {resultWhite: HTMLInputElement, resultBlack: HTMLInputElement};
							results.set(id, [Number(form.resultWhite.value), Number(form.resultBlack.value)]);
							if (form) {
								let parent = form.parentElement as HTMLDivElement;
								if (parent) {
									let nextSibling: HTMLDivElement = parent;
									do {
										nextSibling = nextSibling.nextSibling as HTMLDivElement;
									} while (nextSibling && !nextSibling.getElementsByTagName('form')[0]);
									if (nextSibling) {
										let newForm = nextSibling.getElementsByTagName('form')[0] as HTMLFormElement;
										let newInput = newForm.getElementsByTagName('input')[0] as HTMLInputElement;
										if (newInput) newInput.focus();
									}
								}
							}
							updateResults({});
							props.updatePlayerDict();
							return e;
						}}
					/>
					<div
						className={['playerInfo', styles.playerInfo, styles.playerInfo, b === 'bye' ? styles.bye : ''].join(' ')}
						id={b}
						onClick={(e: React.MouseEvent<HTMLDivElement>) => b === 'bye' ? null : props.setDisplayPlayer((e.target as HTMLDivElement).id)}
					>{bDisplay}</div>
				</div>,
				<PairingDetails
					key={['pairingDetails', w, b].join('.')}
					tournamentID={props.status.id}
					id={id}
					board={i}
					round={round}
					className={['dropdown', styles.pairing].join(' ')}
					expanded={expanded === id}
					gameLinks={gameLinks}
					individual={props.settings.competitors === 'individual'}
					update={getPairings}
					updatePlayerDict={props.updatePlayerDict}
					triggerConfirm={props.triggerConfirm}
				/>
			);
			i++;
		}
		return pairings;
	}

	useEffect(() => {
		getPairings();
	}, [getPairings]);
    
	const listing = useMemo(Listing, [props.playerDict, pairingData, getDisplayName, results, resultsState, props.updatePlayerDict, getPairings, props.setDisplayPlayer, setResults, props.playerDictKey]);

	return (
		<>
			<div className={styles.pairings}>
				<div className={styles.stage}>
					<div className={styles.titleContainer}>
						<div className={['topBanner', styles.topBanner].join(' ')}>
							Pairings
						</div>
					</div>
					<div className={[styles.pairingContainer, 'scrollable'].join(' ')}>
						<div className={[styles.pairingWrapper].join(' ')}>
							{listing}
						</div>
					</div>
				</div>
			</div>
			<div className='bottomBannerBackground'>
				<div className={['bottomBanner', styles.statusBar].join(' ')}>
					<div className={['button'].join(' ')} onClick={props.toggleSidebar}>
						<TiThMenu />
					</div>
					<div style={{display: 'grid', gridAutoFlow: 'column', gridAutoColumns: '1fr', width: '100%'}}>
						{canPairMore ? generatePairButton() : null}
						{generateNextButton()}
					</div>
					<div className={['button'].join(' ')} onClick={props.toggleRightTab}>
						<TiThMenu />
					</div>
				</div>
			</div>
		</>
	);

}