import React, { ReactElement, useState, useEffect, useCallback, useMemo, useReducer } from 'react';
import url from 'url';
import styles from '../css/pairings.module.css';
import componentStyles from '../css/components.module.css';
import { TournamentStatus, ID, Player, PairingObject } from '../interfaces';
import { GET, POST, server } from '../utils/requests';
import { AiOutlineMinus } from 'react-icons/ai';
import { TournamentSettings } from '../utils/settings';

interface PairingsProps {
	userRound: number

	status: TournamentStatus
	playerDict: Map<ID, Player>
	updatePlayerDict: () => void
	playerDictKey: string
	settings: TournamentSettings
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
	id: string
	switchPairings: () => void
}

export default function Switch(props: PairingsProps): ReactElement {

	const round = useMemo(() => props.status.round, [props.status.round]);

	const [pairingData, setPairings] = useState([] as PairingObject[]);
	const [selected, setSelected] = useState([]);
	const [select, deselect] = [
		useCallback((id: string) => selected.length < 2 && !selected.includes(id) ? setSelected([...selected, id]) : null, [selected, setSelected]),
		useCallback((id: string) => {
			if (!selected.includes(id)) return;
			let action = selected.slice(0);
			action.splice(selected.indexOf(id), 1);
			return setSelected(action);
		}, [selected, setSelected])
	];

	const getPairings = useCallback((): Promise<void> => {
		if (!round) return;
		if (!props.playerDict.size) return;
		return GET({ url: '/tournament/' + props.id + '/getPairings' })
			.then((pairings: PairingObject[]) => {
				setPairings(pairings);
			})
			.catch(console.error);
	}, [props.id, setPairings, round, props.playerDict, props.playerDictKey]);

	const getDisplayName = useCallback((id: ID): string => {
		if (id === 'bye') return 'BYE';
		let player = props.playerDict.get(id);
		if (!player) return id;
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [player.firstName, player.lastName].join(' ');
		else displayName = player.firstName;
		return `${displayName} [${player.score}]`;
	}, [props.playerDict]);

	const triggerPairingSwap = useCallback(() => {
		props.triggerConfirm(
			`Really swap pairings ${getDisplayName(selected[0])} and ${getDisplayName(selected[1])}?`,
			() => POST({
				url: url.resolve(server, ['tournament', props.status.id, 'switchPairings'].join('/')),
				data: {
					swapX: selected[0],
					swapY: selected[1],
					round: props.status.round
				}
			}).then(() => {
				setSelected([]);
				props.updatePlayerDict();
			}),
			() => setSelected([])
		)
	}, [selected, props.status.id, props.status.round])

	function generateNextButton(): ReactElement {
		let left = 2 - selected.length;
		return left ? (
			<div className={[styles.waitButton].join(' ')} >
				{`Select ${left} more pairing${left > 1 ? 's' : ''}`}
			</div>
		) : (
			<div
				className={[styles.nextButton].join(' ')}
				onClick={triggerPairingSwap}
			>
				Swap pairings
			</div>
		);
	}

	function Listing(): ReactElement[] {
		let pairings = [] as ReactElement[];
		let dict = props.playerDict;
		for (let [w, b] of pairingData) {
			let [wDisplay, bDisplay] = [getDisplayName(w), getDisplayName(b)];
			let id = w + '.' + b;
			pairings.push(
				<div id={id} className={styles.pairing} key={['pairings', w, b].join('.')} >
					<div
						className={['playerInfo', styles.playerInfo, w === 'bye' ? styles.bye : ''].join(' ')}
						id={w}
						onClick={() => !selected.includes(w) ? select(w) : deselect(w)}
						style={{
							backgroundColor: selected.includes(w) ? 'darkblue' : ''
						}}
					>{wDisplay}</div>
					<div className={componentStyles.resultsBridge}>
						<div className={componentStyles.setResult}>{'\u200b'}</div>
						<div className={componentStyles.neutral} style={{ cursor: 'default' }}>
							<AiOutlineMinus />
						</div>
						<div className={componentStyles.setResult}>{'\u200b'}</div>
					</div>
					<div
						className={['playerInfo', styles.playerInfo, styles.playerInfo, b === 'bye' ? styles.bye : ''].join(' ')}
						id={b}
						onClick={() => !selected.includes(b) ? select(b) : deselect(b)}
						style={{
							backgroundColor: selected.includes(b) ? 'darkblue' : ''
						}}
					>{bDisplay}</div>
				</div>
			);
		}
		return pairings;
	}

	useEffect(() => {
		getPairings();
	}, [getPairings]);

	const listing = useMemo(Listing, [selected, pairingData, getDisplayName, props.updatePlayerDict, getPairings, props.playerDictKey]);

	return (
		<>
			<div className={styles.pairings}>
				<div className={styles.titleContainer}>
					<div className={['topBanner', styles.topBanner].join(' ')}>
						Switch Pairing Mode
					</div>
				</div>
				<div className={[styles.pairingContainer, 'scrollable'].join(' ')}>
					<div className={[styles.pairingWrapper].join(' ')}>
						{listing}
					</div>
				</div>
			</div>
			<div className={['bottomBanner', styles.statusBar].join(' ')}>
				<div className={['button'].join(' ')} style={{ backgroundColor: '#373737', cursor: 'default' }}>
					{'\u200b'}
				</div>
				{generateNextButton()}
				<div className={['button'].join(' ')} style={{ backgroundColor: '#373737', cursor: 'default' }}>
					{'\u200b'}
				</div>
			</div>
		</>
	);

}