export function Crown(props: {
	loading: boolean,
	setLoading: (loading: boolean) => void
	classNames?: StringDictionary
}) {

	return (
		<div
			className={[appStyles.logo, props.classNames?.logo].join(' ')}
		>
			<img style={{visibility: props.loading ? 'hidden' : 'visible'}} className={appStyles.staticLogo} src={process.env.PUBLIC_URL + '/images/favicon.png'} alt='swiss-logo.png'/>

			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={appStyles.staticLogo} src={process.env.PUBLIC_URL + '/images/loading_queen.png'} alt='swiss-logo.png'/>
			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={appStyles.crownOutline} src={process.env.PUBLIC_URL + '/images/crown-outline.png'} alt='outline-gif'/>
			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={appStyles.loadingGif} src={process.env.PUBLIC_URL + '/images/loading-crown.gif'} alt='loading-gif'/>
			{/*
			<div>
				<hr className={appStyles.loadingLine}/>
				<hr className={appStyles.loadingLine}/>
				<hr className={appStyles.loadingLine}/>
			</div>*/}
		</div>
	);
}

interface LoginProps {
	login: (sessionID: string) => void
}
export default function Login(props: LoginProps) {

	const [loading, setLoading] = useState(false);
	const [register, setRegister] = useState(false);
	const [errorMessage, triggerError] = useState('');

	const handleSubmit = useCallback((e: React.FormEvent<HTMLFormElement>): void => {
		setLoading(false);
		FORM(e)
			.then((sessionID: string) => props.login(sessionID))
			.catch((e) => {
				if (e && e.response && e.response.data) triggerError(e.response.data);
				console.error(e);
			})
			.finally(() => {
				setLoading(false);
			});
	}, [setLoading, triggerError, props.login]);

	const loginForm = useMemo((): ReactElement => {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={handleSubmit}
				action={url.resolve(server, 'login')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' className={['input', styles.input].join(' ')}></input>
				<div className={styles.forgottenContainer}>
					<div className={['input', styles.forgotten].join(' ')}>username reminder</div>
					<div className={['input', styles.forgotten].join(' ')}>password reset</div>
				</div>
				<input type='submit' value='LOGIN' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}, [handleSubmit]);

	const registerForm = useMemo((): ReactElement => {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={handleSubmit}
				action={url.resolve(server, 'register')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' minLength={8} className={['input', styles.input].join(' ')}></input>
				<input type='email' name='email' required placeholder='email' className={['input', styles.input].join(' ')} pattern={regexes.email.source}></input>
				<input type='submit' value='REGISTER' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}, [handleSubmit]);

	const loginSection = useMemo((): ReactElement => {
		return (
			<div className={[styles.loginSection, styles.initialiseButton].join(' ')}>
				<div className={styles.loginBackground}>
					<div className={[styles.flip, 'button'].join(' ')} onClick={() => {
						setLoading(true);
						setRegister(false);
						triggerError('');
						setTimeout(() => setLoading(false), 500);
					}}>
						<div>LOGIN</div>
						<div>
                            <IoIosArrowBack />
                        </div>
					</div>
					<div></div>
					<div className={[styles.flip, 'button'].join(' ')} onClick={() => {
						setLoading(true);
						setRegister(true);
						triggerError('');
						setTimeout(() => setLoading(false), 500);
					}}>
						<div>REGISTER</div>
						<div>
                            <IoIosArrowForward />
                        </div>
					</div>
					Background
				</div>
				<div className={[styles.foreground, register ? styles.registerForeground : styles.loginForeground].join(' ')}>
					{
						register ? registerForm : loginForm
					}
				</div>
			</div>
		);
	}, [setLoading, setRegister, triggerError, register]);

	const crownBuild = useMemo(() => <Crown setLoading={(l: boolean) => setLoading(l)} loading={loading} />, [setLoading, loading]);

	return (
		<div className={appStyles.canvas}>
			<div className={appStyles.column} />
			<div className={[appStyles.column, styles.column].join(' ')}>
				{crownBuild}
				{loginSection}
				<div className={styles.messageBar}>
					{errorMessage || '\u200b'}
				</div>
				{/*<div className={['button', styles.initialiseButton].join(' ')}>
					{this.state.register ? <>
						<div className={styles.registerForm}>{this.registerForm}</div>
						<div className={styles.flip} onClick={() => this.setState({ register: false })}>{'LOGIN\n'}</div>
					</> : <>
						<div className={styles.flip} onClick={() => this.setState({ register: true })}>{'REGISTER\n'}</div>
						<div className={styles.loginForm}>{this.loginForm}</div>
					</>}
				</div>*/}
			</div>
			<div className={appStyles.column} />
		</div>
	);


}