import React, { ReactElement, useState, useEffect, Dispatch, SetStateAction, CSSProperties, useCallback, useReducer, useMemo } from 'react';
import { InfoBar, Pairings, Sidebar, Profile, Settings, CrossTable, PasteInput, Switch, Public } from './tournament/';
import { Player, ID, TournamentStatus, TournamentSettings } from './interfaces';
import { GET, POST, cookies } from './utils/requests';

import styles from './css/tournament.module.css';
import TabSection from './components/TabSection';
import { IoIosSettings, IoIosPerson } from 'react-icons/io';
import { MdPublic } from 'react-icons/md';

const defaultColumns = [30.8, 38.4, 30.8];

interface TournamentProps {
	id: string
	closeTournament: () => void
	triggerError: (message: string) => void
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
}

function dispatchEvent(eventName: string): void {
	let event = document.createEvent('HTMLEvents') as Event & { eventName: string };
	event.initEvent(eventName, true, true);
	event.eventName = eventName;
	document.dispatchEvent(event);
}

function useCacheColumns(columns: number[]) {

	const [cachedColumns, setColumns] = useState(columns);

	useEffect(() => {
		if (JSON.stringify(columns) === JSON.stringify(cachedColumns)) return;
		cookies.set('columns', columns.map(nb => nb.toString()).join(','));
		setColumns(columns);
	}, [columns]);

}

function MouseEvents({ columns, boundaries, resize, setColumns, setResize, toggleSidebar, toggleRightTab }: {
	columns: number[],
	boundaries: number[],
	resize: number,
	setColumns: (columns: number[]) => void
	setResize: Dispatch<SetStateAction<number>>,
	toggleSidebar: ({set}: { set?: boolean }) => void,
	toggleRightTab: ({set}: { set: boolean }) => void
}): void {

	const [mouseClick, setMouseClick] = useState(0);

	const handleMouseMove = useCallback((e: MouseEvent) => {
		let percentX = e.x / window.innerWidth * 100;
		if (mouseClick) {
			let prev = (columns.slice(0, mouseClick - 1).reduce((acc, curr) => acc += curr, 0));
			let sum = prev + columns[mouseClick - 1] + columns[mouseClick];
			let left = percentX - prev;
			let right = sum - percentX;
			let threshold = 2;
			if (left < threshold) {
				right += left;
				left = 0;
				if (mouseClick === 1) toggleSidebar({set: false});
				dispatchEvent('mouseup');
			} else
			if (right < threshold) {
				left += right;
				right = 0;
				if (mouseClick === columns.length - 1) {
					dispatchEvent('mouseup');
					toggleRightTab({set: false});
				}
			}
			columns[mouseClick - 1] = left;
			columns[mouseClick] = right;
			setColumns(columns.slice(0));
		} else {
			let threshold = 0.2;
			let index = boundaries.findIndex(b => Math.abs(b - percentX) < threshold);
			if (index === 0 && boundaries[0] === 0) index = -1;
			else if (index === boundaries.length - 1 && boundaries[boundaries.length - 1] === 100) index = -1;
			setResize(index + 1);
		}
	}, [mouseClick, columns, setResize, setColumns, boundaries, toggleSidebar, toggleRightTab]);

	useCacheColumns(columns);

	useEffect(() => {
		document.addEventListener('mousemove', handleMouseMove);
		return () => document.removeEventListener('mousemove', handleMouseMove);
	}, [mouseClick, handleMouseMove]);
	useEffect(() => {
		if (!resize) return;
		let callback = () => setMouseClick(resize);
		document.addEventListener('mousedown', callback);
		return () => document.removeEventListener('mousedown', callback);
	}, [resize, setMouseClick]);
	useEffect(() => {
		let callback = () => setMouseClick(0);
		document.addEventListener('mouseup', callback);
		return () => document.removeEventListener('mouseup', callback);
	}, [resize, setMouseClick]);
}

export default function Tournament(props: TournamentProps): ReactElement {

	const [columns, setColumns] = useReducer((state: number[], action: number[]) => {
		return action;
	}, defaultColumns);
	const [resize, setResize] = useState(0);
	const boundaries = useMemo(() => 	columns.reduce((acc, curr) => {
		acc.push((acc[acc.length - 1] ?? 0) + curr);
		return acc;
	}, [] as number[]).slice(0, -1), [columns]);

	useCacheColumns(columns);
	const [userRound, setRound] = useState(0);
	const [displayPlayer, setDisplayPlayerRaw] = useState('');
	const [crossTable, setCrossTable] = useState(false);
	const [pasteInput, setPasteInput] = useState(false);
	const [lastRoundOverride, setLastRound] = useState(undefined) as [boolean | undefined, Dispatch<SetStateAction<boolean | undefined>>];
	const [nextRound, toggleNextRound] = useState(false);
	const [switchMode, setSwitchMode] = useState(false);
	const toggleCrossTable = () => setCrossTable(!crossTable);

	const switchPairings = useCallback((set?: boolean) => setSwitchMode(set ?? !switchMode), [switchMode]);
	const loadColumns = useCallback(async () => {
		let cols: string = await cookies.get('columns');
		if (cols) setColumns(cols.split(',').map(nb => Number(nb)));
	}, [setColumns]);

	useEffect(() => {
		loadColumns();
	}, [loadColumns]);


	const reduceSidebar = useCallback((state: boolean, { set }: { set?: boolean }): boolean => {
		if (columns[0] === 0) {
			let _columns = columns.slice(0);
			_columns[0] = defaultColumns[0];
			if (_columns[1] < defaultColumns[0]) _columns = defaultColumns as [number, number, number];
			else _columns[1] = _columns[1] - defaultColumns[0];
			setColumns(_columns);
			return true;
		} else return typeof set === 'boolean' ? set : !state;
	}, [columns, setColumns]);

	const reduceRightTab = useCallback((state: boolean, { set }: { set: boolean }): boolean => {
		if (set !== false && boundaries[boundaries.length - 1] === 100) {
			let _columns = columns.slice(0);
			_columns[_columns.length - 1] = defaultColumns[_columns.length - 1];
			if (_columns[_columns.length - 2] < defaultColumns[_columns.length - 2]) _columns = defaultColumns as [number, number, number];
			else _columns[_columns.length - 2] = _columns[_columns.length - 2] - defaultColumns[_columns.length - 1];
			setColumns(_columns);
			return true;
		} else {
			return typeof set === 'boolean' ? set : !state;
		}
	}, [columns, setDisplayPlayerRaw, boundaries]);
	

	const [showRightTab, toggleRightTab] = useReducer(reduceRightTab, true);
	const [rightTab, setRightTab] = useState('settings' as 'settings' | 'public' | 'player');
	const [sidebar, toggleSidebar] = useReducer(reduceSidebar, true);

	const setDisplayPlayer = useCallback((id: string) => {
		if (!showRightTab) toggleRightTab({ set: true });
		if (rightTab !== 'player') setRightTab('player');
		setDisplayPlayerRaw(id);
	}, [setDisplayPlayerRaw, rightTab, setRightTab, showRightTab, toggleRightTab]);

	const [playerDictKey, setPlayerDictKey] = useState(Math.random().toString(16).slice(2, 9));

	const [status, setStatus] = useState({} as TournamentStatus);
	const [settings, setSettings] = useState({} as TournamentSettings);
	const [rounds, setRounds] = useState([] as Date[]);
	const [players, setPlayers] = useState([] as Player[]);

	MouseEvents({ columns, boundaries, resize, setColumns, setResize, toggleSidebar, toggleRightTab});

	/* Fetch Data */
    
	const [setDictTimeout, awaitTimeout] = useState(false);
	const [lastUpdate, setLastUpdate] = useState(Date.now());

	const reducePlayerDict = useCallback((state: Map<ID, Player>, action: {
		id?: ID,
		player?: Player,
		players?: Player[],
		hash?: string
	}) => {
		if (action.id) {
			state.delete(action.id);
			if (action.id === displayPlayer) setDisplayPlayer('');
			return state;
		}
		let players: Player[];
		if (action.players) players = action.players;
		else players = [action.player];
		for (let p of players) state.set(p.id, p);
		setPlayerDictKey(action.hash || Math.random().toString(16).slice(2, 9));
		return state;
	}, [setDisplayPlayer, setPlayerDictKey, displayPlayer, lastUpdate]);

	const [playerDict, setPlayerDict] = useReducer(reducePlayerDict, new Map() as Map<ID, Player>);

	const updatePlayerDict = useCallback((force?: boolean): Promise<void> => {
		if (!force && (Date.now() - lastUpdate < 3000)) {
			setLastUpdate(Date.now());
			awaitTimeout(true);
			return Promise.resolve();
		}
		return GET({ url: '/tournament/' + props.id + '/fetchPlayers'})
			.then((players: Player[]) => {
				setLastUpdate(Date.now());
				awaitTimeout(false);
				setPlayers(players);
				setPlayerDict({ players });
			});
	}, [props.id, setPlayers, setPlayerDictKey, setPlayerDict, lastUpdate]);
    
	useEffect(() => {
		if (!setDictTimeout) return;
		let x = setTimeout(updatePlayerDict, 5000);
		return () => clearTimeout(x);
	}, [setDictTimeout, updatePlayerDict]);

	const updateTournamentStatus = useCallback((): Promise<void> => GET({ url: '/tournament/' + props.id + '/getStatus'})
		.then(setStatus), [props.id, setStatus]);

	const updateTournamentSettings = useCallback((): Promise<void> => GET({ url: '/tournament/' + props.id + '/getSettings'})
		.then(setSettings), [props.id, setSettings]);

	const updateSetting = useCallback((k: string, v: any): Promise<void> => POST({
		url: 'tournament/' + props.id + '/updateSetting',
		data: {
			[k]: v
		}
	}).then(updateTournamentSettings), [props.id, updateTournamentSettings]);

	const updateRounds = useCallback((): Promise<void> => GET({ url: '/tournament/' + props.id + '/fetchRounds'})
		.then(setRounds), [props.id, setRounds]);
        
	const checkIfEnoughPlayers = useCallback(async (): Promise<void> => {
		toggleNextRound(players.filter(p => p.active !== false).length >= 2);
	}, [players, toggleNextRound, updatePlayerDict]);
	
	useEffect(() => {
		if (!props.id) return null;
		updateRounds();
		updateTournamentStatus();
		updateTournamentSettings();
		updatePlayerDict(true);
	}, [props.id, updateRounds, updateTournamentStatus, updateTournamentSettings]);//, updateRounds, updateTournamentStatus, updateTournamentSettings, updatePlayerDict]);

	if (!props.id) return null;

	if (crossTable) return (
		<div className={styles.tournament}>
			<CrossTable
				show={toggleCrossTable}
				setDisplayPlayer={setDisplayPlayer}
				status={status}
				players={players}
				settings={settings}
				rightTab={showRightTab}
				toggleRightTab={() => toggleRightTab({set: !showRightTab })}
			/>
			{showRightTab ? 
				<div className={styles.profile}>
					<TabSection
						name={rightTab}
						setName={(name: string) => setRightTab(name as any as 'settings' | 'player' | 'public')}
						nameDict={{
							player: <><IoIosPerson />Profile</>,
						}}
					/>
					<Profile
						id={props.id}
						playerID={displayPlayer}
						setDisplayPlayer={setDisplayPlayer}
						playerDict={playerDict}
						modifyPlayerDict={(player: Player, hash: string) => setPlayerDict({ player, hash })}
						settings={settings}
					/>
				</div> :
				null}
		</div>		
	);

	if (pasteInput) return (
		<div className={styles.tournament}>
			<PasteInput
				closePasteInput={() => setPasteInput(false)}
				status={status}
				settings={settings}
				id={props.id}
				updatePlayerDict={updatePlayerDict}
			/>
		</div>		
	);

	let style: CSSProperties = {};
	if (resize) style.cursor = 'ew-resize';

	return (
		<div className={styles.tournament} style={{
			gridTemplateColumns: columns.join('% ') + '%',
			...style
		}}>
			{!sidebar ? null : <Sidebar
				playerDict={playerDict}
				playerDictKey={playerDictKey}
				updatePlayerDict={updatePlayerDict}
				setPlayerDict={setPlayerDict}
				
				settings={settings}
				id={props.id}
				setDisplayPlayer={setDisplayPlayer}
				checkIfEnoughPlayers={checkIfEnoughPlayers}
				togglePasteInput={() => setPasteInput(true)}
			/>}
			<InfoBar
				userRound={userRound}
				setRound={setRound}
				
				status={status}
				settings={settings}
				updateTournamentStatus={updateTournamentStatus}
				round={status.round}
				totalRounds={settings.totalRounds}
				closeTournament={props.closeTournament}
				toggleCrossTable={toggleCrossTable}

				setLastRound={setLastRound}
				lastRoundOverride={lastRoundOverride}
				triggerError={props.triggerError}
				switchPairings={switchPairings}
			/>
			{switchMode ? <Switch
				userRound={userRound}
				status={status}
				playerDict={playerDict}
				updatePlayerDict={updatePlayerDict}				
				settings={settings}
				id={props.id}
				playerDictKey={playerDictKey}
				triggerConfirm={props.triggerConfirm}
				switchPairings={switchPairings}
			/> : <Pairings
				userRound={userRound}
				status={status}
				playerDict={playerDict}
				updatePlayerDict={updatePlayerDict}
				
				settings={settings}

				id={props.id}
				canTriggerNextRound={nextRound}
				toggleSidebar={() => toggleSidebar({})}
				toggleRightTab={() => toggleRightTab({set: !showRightTab})}
				setDisplayPlayer={setDisplayPlayer}
				updateRound={updateTournamentStatus}
				setLastRound={setLastRound}
				lastRoundOverride={lastRoundOverride}
				toggleCrossTable={toggleCrossTable}
				playerDictKey={playerDictKey}
				triggerConfirm={props.triggerConfirm}
			/>}
			{showRightTab ? 
				<div className={styles.profile}>
					<div className={styles.profileStage}>
						<TabSection
							name={rightTab}
							setName={(name: string) => setRightTab(name as any as 'settings' | 'player' | 'public')}
							nameDict={{
								settings: <><IoIosSettings style={{color: 'lightgrey'}} />Settings</>,
								player: <><IoIosPerson style={{color: '#385898'}} />Profile</>,
								public: settings.public ? <><MdPublic style={{color: 'green'}} />Public</> : ''
							}}
						/>
						{
							rightTab === 'settings' ?
								<Settings
									updateTournamentSettings={updateTournamentSettings}
									updateSetting={updateSetting}
									status={status}
									settings={settings}
									setLastRound={setLastRound}
									triggerError={props.triggerError}
									players={players}
									updateRounds={updateRounds}
									rounds={rounds}
								/> :
								rightTab === 'player' ?
									<Profile
										id={props.id}
										playerID={displayPlayer}
										setDisplayPlayer={setDisplayPlayer}
										playerDict={playerDict}
										modifyPlayerDict={(player: Player, hash: string) => setPlayerDict({ player, hash })}
										settings={settings}
										playerDictKey={playerDictKey}
									/> :
									rightTab === 'public' ?
										<Public
											updateTournamentSettings={updateTournamentSettings}
											updateSetting={updateSetting}
											status={status}
											settings={settings}
											setLastRound={setLastRound}
											triggerError={props.triggerError}
											players={players}
											updateRounds={updateRounds}
											rounds={rounds}
										/> :
										null
						}
					</div>
				</div>
				: null}
		</div>
	);

}