import React, { ReactElement, useEffect, useState, useMemo, useCallback } from 'react';
import { EditableField } from '../components/';
import { TournamentSettings, TournamentStatus, Player } from '../interfaces';
import url from 'url';

import styles from '../css/profile.module.css';
import { server, POST } from '../utils/requests';
import { defaultSettings, TournamentSetting } from '../utils/settings';
import { capitalise } from '../utils/prototype';
import { Bottom } from '../components/Buttons';
import { IoMdClose } from 'react-icons/io';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';

interface SettingsProps {
	updateTournamentSettings: () => Promise<void>
	updateSetting: (k: string, v: any) => Promise<void>
	updateRounds: () => Promise<void>
	status: TournamentStatus
	settings: TournamentSettings
	setLastRound: (override: boolean) => void // need to include a method to cancel a last round token when the total rounds is incremented
	triggerError: (message: string) => void
	players: Player[]
	rounds: Date[]
}

const watchProps = Object.values(defaultSettings).reduce((acc, curr) => {
	for (let def of Object.values(curr)) {
		if (def.show) acc.push(...Object.keys(def.show));
	}
	return acc;
}, []) as (keyof TournamentSettings)[];

function modifyHeightValues (): void {
	let headers = Array.from(document.querySelectorAll('.' + [styles.sectionListWrapper].join('.'))) as HTMLDivElement[];
	for (let h of headers) {
		let list = h.firstElementChild as HTMLDivElement;
		h.style.height = h.className.includes(styles.expanded) ? list.getBoundingClientRect().height + 'px' : '0px';
	}
}

export default function Settings(props: SettingsProps): ReactElement {

	const [sectionExpando, setSection] = useState('general');

	const handleToggle = useCallback((k: string, v: any, def: {
		name?: string,
		enum?: string[],
		convert?: (v: any) => string,
		type?: string,
		locked?: boolean,
		lockOnInit?: boolean
	}) => {
		try {
			let value: any;
			if (def.enum) {
				let index = def.enum.indexOf(v);
				if (index === -1) console.error('Invalid setting value ' + k + ': ' + v);
				let setIndex = (index + 1) % def.enum.length;
				value = def.enum[setIndex];
			} else
			if (def.type === 'boolean') {
				value = !v;
			} else
			if (def.type === 'number') {

				if (v === 'Infinity') value = Infinity as number;
				else if (v === '') value = Infinity as number;
				else v = Number(v) as number;

				if (k === 'totalRounds') {
					if (v <= props.status.round) throw new Error('Can\'t make total number of rounds less than current round number');
					if (v !== Infinity && v >= 100) throw new Error('Can\'t make total number of rounds more than 100');
				}
				
				return v;
			}
			props.updateSetting(k, value);
		} catch (e) {
			if (e && e.message) props.triggerError(e.message);
		}
	}, [props.status.round, props.updateSetting, props.triggerError]);
	
	const watchedSettings = useMemo(() => Object.entries(props.settings).filter(([k]) => watchProps.includes(k as keyof TournamentSettings)), [props.settings]);
    
	useEffect(() => {
		modifyHeightValues();
	}, [JSON.stringify(watchedSettings)]);

	const renderEntry = useCallback((([k, def]: [keyof TournamentSettings, TournamentSetting], i: number): ReactElement => {
		let hidden = false;
		if (def.show) {
			for (let [k, v] of Object.entries(def.show)) {
				let value = props.settings[k as keyof TournamentSettings];
				if (value !== v) hidden = true;
			}
		}
		if (hidden) return null;
        
		let locked = false;
		if (def.locked) locked = true;
		else if (def.lockOnInit && props.status.round) locked = true;
        
		let v = props.settings[k as keyof TournamentSettings] ?? def.defaultValue;
		let content = def.convert && !def.value ? def.convert(v) : v as string;
		return (
			<div className={styles.row} key={['row', i].join('.')}>
				<div className={['fieldBox', def.show ? styles.semiPresent : ''].join(' ')}>
					{def.name}
					{def.name.endsWith('?') ? '' : ':'}
				</div>
				{(() => {
					if (locked || def.value) {
						if (def.value) content = def.value(props.status, props.players, props.settings);
						if (def.convert) content = def.convert(content);
						return <div
							style={{
								cursor: def.type === 'link' ? 'copy' : 'default'
							}}
							className={['fieldBox', styles.locked].join(' ')}
							onClick={def.type !== 'link' ? null : () => navigator.clipboard.writeText(content)}
						>
							{typeof content !== 'undefined' ? content.toString() : '\u200b'}
						</div>;
					} else
					if (def.type === 'string') {
						return <EditableField
							handleSubmit={props.updateSetting}
							content={content}
							url={url.resolve(server, ['tournament', props.status.id, def.action || 'updateSetting'].join('/'))}
							inputType='text'
							onSubmitSuccess={(e) => {
								props.updateTournamentSettings();
								return e;
							}}
							additionalStyles={{
								editableField: styles.editableField,
								input: styles.input
							}}
							name={k}
							additionalInputProps={{}}
							show={{
								submit: true,
								...def.allow
							}}
						/>;
					} else
					if (def.type === 'number') {
						return <EditableField
							handleSubmit={props.updateSetting}
							content={content}
							url={url.resolve(server, ['tournament', props.status.id, 'updateSetting'].join('/'))}
							inputType='number'
							onSubmitSuccess={(e) => {
								props.updateTournamentSettings();
								return e;
							}}
							additionalStyles={{
								editableField: styles.editableField,
								input: styles.input
							}}
							name={k}
							additionalInputProps={def.nb}
							show={{
								submit: true,
								increment: true,
								...def.allow
							}}
						/>;
					} else {
						return <div
							className={['fieldBox', styles.editable].join(' ')}
							onClick={() => handleToggle(k, v, def)}
						>
							{typeof content !== 'undefined' ? content.toString() : '\u200b'}
						</div>;
					}
				})()}
			</div>
		);
	}), [props.settings, props.status, props.players, props.updateSetting, props.status.id, props.updateTournamentSettings]);

	let roundEntries = [];
	let length = props.settings.totalRounds === Infinity || props.settings.totalRounds === 'Infinity' ? Math.max(props.rounds.length, 1) : props.settings.totalRounds;
	for (let i = 1; i <= length; i++) roundEntries.push(i);

	return (
		<>
			<div className={[styles.profileContainer, 'scrollable'].join(' ')}>
				{Object.entries(defaultSettings).map(([section, data]): ReactElement => {
					if (section == 'public') return null;
					return (
						<CollapsableSection
							key={section}
							value={section}
							header={capitalise(section)}
							expanded={sectionExpando === section}
							setExpanded={(state: boolean, action: string) => setSection(state ? action : '')}
						>
							{(Object.entries(data) as [keyof TournamentSettings, TournamentSetting][])
								.map(renderEntry)}
						</CollapsableSection>
					);
				})}
				<SectionBreak />
			</div>
		</>
	);
}