import React, { ReactElement } from 'react';
import styles from '../css/components.module.css';

interface TabProps {
	name: string
	nameDict: {[key: string]: string | JSX.Element }
	setName: (name: string) => void
}

export default function TabSection(props: TabProps): ReactElement {

	return (				
		<div className={[styles.tabSection].join(' ')}>
			{Object.entries(props.nameDict).map(([k, v]) => {
				if (!v) return null;
				return <div
					onClick={() => props.setName(k as string)}
					className={[styles.tab, k === props.name ? styles.active : ''].join(' ')}
				>
					{v}
				</div>
			})}
		</div>
	);
}