import { User } from "../interfaces";
import { daysBetween } from "./prototype";


export const defaultUserSettings = {
	username: {
		name: 'Username',
		type: 'string',
		locked: true,
		primaryKey: true,
	},
	email: {
		name: 'Email',
		type: 'string',
		locked: true,
		defaultValue: ''
	},
    licensed: {
        name: 'License',
        type: 'number',
        defaultValue: 0,
        locked: true,
        convert: (v: number) => {
            if (v === 1) return 'Trial';
            if (v === 2) return 'Licensed';
            if (v === 3) return 'Admin';
            return 'Free';
        }
    },
    daysRemaining: {
        name: 'Trial time',
        type: 'number',
        locked: true,
        value: (user: User) => Math.floor(30 - daysBetween(new Date(user.lastLicensed), new Date(Date.now()))),
		convert: (v: number) => v + ' days',
        validate: (v: string) => !!v
    },
    lastLicensed: {
        name: 'Last licensed',
        type: 'date',
        defaultValue: null,
        locked: true,
        convert: (v: string) => new Date(v).toString().slice(0, 21),
        validate: (v: string) => !!v
    },
	createdAt: {
		name: 'Account created',
		convert: (v: string) => new Date(v).toString().slice(0, 21),
		locked: true,
		defaultValue: Date.now()
	},
	updatedAt: {
		name: 'Last seen',
		convert: (v: string) => new Date(v).toString().slice(0, 21),
		locked: true,
		defaultValue: Date.now()
	}
} as {
	[key: string]: {
		name?: string,
		enum?: string[],
        convert?: (v: any) => string,
        validate?: (v: any) => boolean
        value?: (user: User) => any
		type?: string
		locked?: boolean

		primaryKey?: boolean
		defaultValue?: any
	}
};