import React, { ReactElement, useEffect, CSSProperties, useState, ReactNode, useRef, useCallback } from 'react';
import styles from '../css/components.module.css';
import { FaAngleLeft } from 'react-icons/fa';

interface SectionProps {
    children?: ReactNode
    header: string | ReactElement
    useInternalState?: boolean
    value?: string
    expanded?: boolean
    setExpanded?: (state: boolean, action: string) => void
    style?: CSSProperties
    id?: string
}

export function SectionBreak() {

	return (
		<div className={styles.sectionContainer}>
			<div className={[styles.sectionHeader, 'topBanner'].join(' ')}>
				<div className={styles.sectionHeaderText} style={{cursor: 'default'}} />
			</div>
		</div>
	);
}

export default function CollapsableSection(props: SectionProps) {

	const [internalExpanded, setExpanded] = useState(props.expanded !== undefined ? props.expanded : true);
	const [height, setHeight] = useState(0);

	const innerDiv = useRef() as React.RefObject<HTMLDivElement>;

	let expanded = props.useInternalState ? internalExpanded : props.expanded;
	
	const updateHeight = useCallback(() => {
		if (!innerDiv.current) return;
		setHeight(expanded ? innerDiv.current.getBoundingClientRect().height : 0);
	}, [setHeight, expanded, innerDiv.current]);
	useEffect((): void => {
		updateHeight();
	}, [updateHeight, props.children]);
	useEffect(() => {
		let x;
		for (let child of Array.from(innerDiv.current.children)) {
			if ('onload' in child) x = (child as HTMLImageElement).onload = updateHeight;
			for (let grandChild of Array.from(child.children)) {
				if ('onload' in grandChild) x = (grandChild as HTMLImageElement).onload = updateHeight;
			}
		}
	}, [innerDiv.current, updateHeight]);

	return (
		<div className={styles.sectionContainer} style={props.style}>
			<div className={[styles.sectionHeader, 'topBanner'].join(' ')}>
				<div className={styles.sectionHeaderText} onClick={() => props.useInternalState ? setExpanded(!expanded) : props.setExpanded(!expanded, props.value)}>
					{props.header}
				</div>
				{!props.children ?
					null : 
					<div className={styles.sectionToggle} onClick={() => props.useInternalState ? setExpanded(!expanded) : props.setExpanded(!expanded, props.value)}>
						<FaAngleLeft style={{
							transform: expanded ? 'rotate(-90deg)' : 'rotate(0deg)'
						}}/>
					</div>
				}
			</div>
			<div className={[styles.sectionListWrapper, expanded ? styles.expanded : ''].join(' ')} style={{
				height
			}}>
				<div ref={innerDiv} className={[styles.sectionList].join(' ')} id={props.id}>
					{props.children}
				</div>
			</div>
		</div>
	);
}