import React, { ReactElement } from 'react';

export function Bottom(...arr: [
    (() => Promise<void> | void),
    ReactElement | string,
    string
][]): ReactElement[] {
    let buttons = [] as ReactElement[];
    for (let item of arr) {
        if (!item) continue;
        let [func, icon, name] = item;
        buttons.push(<div key={['button', name.toLowerCase()].join('.')} onClick={func} className='button'>
            {icon}{' '}{name}
        </div>);
    }
    return buttons;
}