import { capitalise } from './prototype';
import { TournamentStatus, Player } from '../interfaces';
import { resultsServer } from './requests';

export interface TournamentSettings {

	tournamentId: string; // Note that the `null assertion` `!` is required in strict mode.
	pairingSystem: string;
	competitors: string
	totalRounds: number | 'Infinity';
	performanceSystem: 'ELO' | 'FIDE';
	ratingSystem: 'ECF' | 'FIDE' | 'USCF';

	displayPoints: 'match' | 'game';
	gamePointTotal: number;
	lossReward: number;
	drawReward: number;
	winReward: number;
	byeReward: 'win' | 'draw' | 'loss';

	public: boolean
	vanityURL: string
	logoURL: string
	profileURL: string
	rulesURL: string
	streamURL: string
	showRatings: boolean
	allowNewPlayers: boolean;
    allowSelfSubmissions: boolean;
    allowClashes: boolean;
	roundTimes: boolean;

	createdAt: string
	updatedAt: string

}

export interface TournamentSetting {
	name?: string
	enum?: string[]
	convert?: (v: any) => string
	value?: (status: any, players: Player[], setting: TournamentSettings) => any
	type?: string
	locked?: boolean,
	lockOnInit?: boolean
	show?: {
        [key in keyof TournamentSettings]?: any
    }
	allow?: {
		infinity?: boolean
		any?: boolean
		submit?: boolean
        increment?: boolean
        clear?: boolean
	}
	nb?: {
		min?: number
		max?: number
		step?: number
	}
	action?: string
	primaryKey?: boolean
	defaultValue?: any
}

export const defaultSettings = {
	general: {
		tournamentId: {
			name: 'Unique ID',
			type: 'string',
			locked: true,
			primaryKey: true
		},
		status: {
			name: 'Status',
			enum: ['uninitialised', 'active', 'finished'],
			convert: capitalise,
			defaultValue: 'uninitialised',
			value: (status: TournamentStatus): string => {
				if (!status.round) return 'uninitialised';
				if (status.active === false) return 'finished';
				else return 'active';
			}
		},
		pairingSystem: {
			name: 'Pairing system',
			enum: ['swiss'],
			convert: capitalise,
			lockOnInit: true,
			defaultValue: 'swiss'
		},
		competitors: {
			name: 'Tournament type',
			enum: ['individual', 'team'],
			convert: capitalise,
			lockOnInit: true,
			defaultValue: 'individual'
		},
		totalRounds: {
			name: 'Total rounds',
			convert: (v: number | 'Infinity') => v === 'Infinity' ? '' : v.toString(),
			type: 'number',
			defaultValue: Infinity,
			nb: {
				min: 1,
				max: 100
			},
			allow: {
				infinity: true
			}
		},
		ratingSystem: {
			name: 'Rating system',
			enum: ['ECF', 'FIDE', 'USCF'],
			lockOnInit: true,
			defaultValue: 'FIDE'
		},
		displayPoints: {
			name: 'Show',
			convert: (v: 'match' | 'game') => capitalise(v) + ' Points',
			enum: ['match', 'game'],
			defaultValue: 'match'
		},
		gamePointTotal: {
			name: 'Sum results to',
			type: 'number',
			defaultValue: 1,
			show: {
                displayPoints: 'game'
            },
			lockOnInit: true,
			nb: {
				step: 1,
				min: 1
			},
			allow: {
				any: true
			}
		},
		public: {
            name: 'Public',
            convert: (v: boolean) => capitalise(v.toString()),
            lockOnInit: false,
            type: 'boolean',
            defaultValue: false
        }
	},
	permissions: {        
		allowNewPlayers: {
			name: 'Mid-way joining?',
			convert: (v: boolean) => capitalise(v.toString()),
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true
		},
		allowSelfSubmissions: {
			name: 'User submissions?',
			convert: (v: boolean) => capitalise(v.toString()),
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true
		},
		allowClashes: {
			name: 'Forbid certain pairings?',
			convert: (v: boolean) => capitalise(v.toString()),
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true
        }
	},
	points: {
		lossReward: {
			name: 'Points on loss',
			lockOnInit: true,
			type: 'number',
			defaultValue: 0,
			nb: {
				step: 0.1
			}
		},
		drawReward: {
			name: 'Points on draw',
			lockOnInit: true,
			type: 'number',
			defaultValue: 0.5,
			nb: {
				step: 0.1
			}
		},
		winReward: {
			name: 'Points on win',
			lockOnInit: true,
			type: 'number',
			defaultValue: 1,
			nb: {
				step: 0.1
			}
		},
		byeReward: {
			name: 'Bye reward',
			enum: ['win', 'draw', 'loss'],
			lockOnInit: true,
			defaultValue: 'win'
		},
		performanceSystem: {
			name: 'Performance rating',
			enum: ['ELO', 'FIDE'],
			lockOnInit: false,
			defaultValue: 'ELO'
		}
	},
	stats: {
		nbPlayers: {
			name: 'Number of players',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.length,
			convert: (v: number) => v.toString()
		},
		activePlayers: {
			name: 'Active players',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.filter(p => p.active).length,
			convert: (v: number) => v.toString()
		},
		matchesPlayed: {
			name: 'Matches played',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.reduce((acc, curr) => acc += curr.played, 0) / 2,
			convert: (v: number) => v.toString()
		},
		gamesPlayed: {
			name: 'Games played',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.reduce((acc, curr) => acc += curr.histories.reduce((acc, curr) => curr ? acc += curr.played : acc, 0), 0) / 2,
			convert: (v: number) => v.toString()
		},
		averageRating: {
			name: 'Average rating',
			type: 'number',
			defaultValue: 1200,
			value: (status: TournamentStatus, players: Player[]): number => {
				let sum = players.reduce((acc, curr) => acc += curr.rating, 0);
				return Math.round(sum / players.length);
			},
			convert: (v: number) => v.toString()
		},
		createdAt: {
			name: 'Created at',
			convert: (v: string) => new Date(v).toString().slice(0, 24),
			locked: true
		},
		updatedAt: {
			name: 'Last updated',
			convert: (v: string) => new Date(v).toString().slice(0, 24),
			locked: true
		}
	},
	public: {
		vanityURL: {
			name: 'Custom ID',
			type: 'string',
            defaultValue: '',
			action: 'updateVanityURL'
		},
		url: {
			name: 'URL',
			type: 'link',
            defaultValue: '',
			value: (status: TournamentStatus, p: Player[], s: TournamentSettings): string => `${resultsServer}tournament/${s.vanityURL || status.id}`.trim()
		},
		profileURL: {
			name: 'Link to website',
			type: 'string'
		},
		rulesURL: {
			name: 'Link to rules',
			type: 'string'
		},
		logoURL: {
			name: 'Link to profile picture',
			type: 'string'
		},
		streamURL: {
			name: 'Stream link',
			type: 'string'
		},
		showRatings: {
			name: 'Show ratings',
			convert: (v: boolean) => capitalise(v.toString()),
			type: 'boolean',
			defaultValue: true
		},
		roundTimes: {
			name: 'Round times?',
			convert: (v: boolean) => capitalise(v.toString()),
			type: 'boolean',
			defaultValue: false,
			locked: false
		}
	}
} as {
	[key: string]: {
		[key: string]: TournamentSetting
	}
};