import React, { ReactElement, useState, useReducer, useCallback, useEffect, useMemo } from 'react';
import './App.css';
import Login from './tournament/Login';
import StatusBar from './StatusBar';
import Tournament from './Tournament';
import { POST, cookies, setErrorCallback } from './utils/requests';
//import { isElectron, isDev, isProd } from './utils/electron';

import Menu from './tournament/Menu';
import Alert from './tournament/Alert';
import { useStateWithLabel } from './utils/prototype';

interface AlertAction {
	message: string
	title?: string
	error?: boolean
	confirm?: boolean
	resolve?: () => void
	reject?: () => void
	close?: () => void
}

export default function App(): ReactElement {

	const [sessionID, setSession] = useState('');
	const [tournamentID, setTournament] = useState('');
	const [mode, setMode] = useStateWithLabel('login' as 'login' | 'menu' | 'tournament' | 'grid', 'mode');
	const [alertBox, setAlertBox] = useReducer((state: AlertAction, action: AlertAction) => {
		return Object.assign({}, state, action);
	}, { message: '', title: '', error: true, confirm: false });

	const closeAlertBox = useCallback((): void => setAlertBox({ message: '' }), [setAlertBox]);
	const triggerConfirm = useCallback((message: string, resolve: () => void, reject: () => void) => setAlertBox({
		message,
		title: 'CONFIRM ACTION',
		confirm: true,
		resolve,
		reject
	}), [setAlertBox]);
	const triggerError = useCallback((message: string) => setAlertBox({message, title: 'ERROR', confirm: false }), [setAlertBox]);

	const setTournamentID = useCallback((tournamentID: string): void => {
		setTournament(tournamentID);
		setMode(tournamentID ? 'tournament' : 'menu');
	}, [setTournament, setMode]);

	const hasLoggedIn = useCallback(async (sessionID: string): Promise<void> => {
		await cookies.set('sessionID', sessionID, { path: '/' });
		setSession(sessionID);
		setMode('menu');
	}, [setSession, setMode]);

	const closeTournament = useCallback(async (): Promise<void> => {
		await cookies.set('tournamentID', '', { path: '/' });
		setMode('menu');
		setTournament('');
	}, [setTournament, setMode]);

	const endSession = useCallback(async (): Promise<void> => {
		try {
			await cookies.set('sessionID', '', { path: '/' });
			await POST({ url: '/endSession' });
		} catch (e) {
			console.error(e);
		} finally {
			setSession('');
			setMode('login');
		}
	}, [setSession, setMode]);
	
	useEffect(() => {
		setErrorCallback((errorMessage: string) => triggerError(errorMessage));
		return () => setErrorCallback(() => {});
	}, [triggerError]);

	const pageLoad = useCallback(async () => {
		let sessionID = await cookies.get('sessionID');
		if (!sessionID) return;
		setSession(sessionID);
		setMode('menu');
	}, [setSession, setMode]);

	useEffect(() => {
		pageLoad();
	}, [pageLoad]);

	const Mode: ReactElement = useMemo(() => {
		switch (mode) {
		case 'login': {
			return <Login login={hasLoggedIn}/>;
		}
		case 'menu': {
			return <Menu
				endSession={endSession}
				setTournamentID={setTournamentID}
				triggerConfirm={triggerConfirm}
			/>;
		}
		case 'tournament': {
			return <Tournament
				id={tournamentID}
				closeTournament={closeTournament}
				triggerError={triggerError}
				triggerConfirm={triggerConfirm}
			/>;
		}
		default: {
			console.error('invalid mode');
			setMode('login');
			return null;
		}
		}
	}, [mode, hasLoggedIn, sessionID, endSession, setTournamentID, triggerConfirm, tournamentID, closeTournament, triggerError, setMode]);

	return (
		<>
			<StatusBar/>
			<div className='stage' style={{
				backgroundImage: `url(${process.env.PUBLIC_URL}/images/leaves-texture.png)`
			}}>
				{Mode}
				{alertBox.message && alertBox.message.length > 10 ? <Alert
					{...alertBox}
					close={closeAlertBox}
				/> : null}
			</div>
		</>
	);

}