export const isElectron = typeof window.require === 'function'; //detect !in-browser
export const isDev = process.env.NODE_ENV === 'development'; //detects a react dev build
export const isProd = typeof window.require === 'function' && !window.require('electron-is-dev'); //detect an electron prod build

// export const renderedArguments = !window.process.argv.includes('--production');
/**
 * 				|		isDev						|					!isDev	
 * _________________________________________________________________________________________________
 * !isElectron	|		yarn start (in-browser)		|		yarn deploy (gh-pages build)
 *  isElectron	|		yarn gui (linked electron)	|		yarn gui-prod (headless electron build)
 * 
 * isProd: yarn exe (OS-specific executable)
 */

export const remote = isElectron ? window.require('electron').remote : null;
export const session = isElectron ? remote.session : null;

export const win = (isElectron ? remote.getCurrentWindow() : {
	minimize: () => {},
	maximize: () => {},
	restore: () => {},
	close: () => {},
	isMaximized: () => true		
}) as any;

export const electronCookies = isElectron ? win.webContents.session.cookies : null;

console.log('isDev', isDev);
console.log('isElectron', isElectron);
console.log('isProd', isProd);